import axios from 'axios';
import React, { Component } from 'react';
import ProfileCards from '../components/ProfileCards';
require('dotenv').config();
const REACT_APP_BACK_END_URL = process.env.REACT_APP_BACK_END_URL;
export default class Profile extends Component {
  constructor(props) {
    super(props);
    this.state = {
      booksData: [],
    };
  }

  handleUpdateBook = () => {};

  handleDeleteBook = (bookId) => {
    axios
      .delete(`${REACT_APP_BACK_END_URL}/books/${bookId}`)
      .then((deleteResponse) => {
        console.log(deleteResponse);
        if (deleteResponse.data.deletedCount === 1) {
          const newBooksArray = this.state.booksData.filter(
            (Book) => Book.book_id !== bookId
          );

          this.setState({ booksData: newBooksArray });
        }
      })
      .catch((error) => {
        console.log(error);
      });
  };
  componentDidMount = () => {
    axios
      .get(`${REACT_APP_BACK_END_URL}/books`)
      .then((booksResponse) => {
        this.setState({ booksData: booksResponse.data });
      })
      .catch((error) => {
        console.log(error);
      });
  };

  render() {
    return (
      <div>
        <ProfileCards
          booksData={this.state.booksData}
          handleDeleteBook={this.handleDeleteBook}
        />
      </div>
    );
  }
}
